.SinglePost_card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 15px;
  height: 100%;
  position: relative;
}
.SinglePost_media {
  height: 0px;
  padding-top: 56.25%;
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: darken;
}
.SinglePost_overlay {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
}
.SinglePost_overlay2 {
  position: absolute;
  top: 20px;
  right: 20px;
  color: white;
}

.SinglePost_details {
  display: flex;
  justify-content: space-between;
  margin: 20px 20px 20px 6px;
}

.SinglePost_cardActions {
  padding: 0 16px 8px 16px;
  display: flex;
  justify-content: space-between;
}
