.NavBar_appbar {
  border-radius: 5px;
  margin: 30px 0px;
  display: flex;
  flex-direction: row !important;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.NavBar_heading {
  color: rgba(0, 183, 255, 1);
}
.NavBar_brandContainer {
  display: flex;
  align-items: center;
}
.NavBar_toolbar {
  display: flex;
  justify-content: flex-end;
  width: 400px;
}
.NavBar_image {
  margin-left: 15px;
}
.NavBar_profile {
  display: flex;
  justify-content: space-between;
  width: 250px;
}
.NavBar_purple {
  color: white !important;
  background-color: #9c27b0 !important;
}
.NavBar_userName {
  display: flex;
  align-items: center;
}
